* {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  background-image: url(./assets/images/background.jpg);
  background-position: center; }

ul,
li {
  margin: 0;
  padding: 0; }

.container {
  width: 960px;
  height: 640px;
  margin: 50px auto;
  border: 5px solid #c7c6c6;
  border-radius: 10px;
  overflow: hidden;
  position: relative; }

.sliders {
  width: 4800px;
  height: 640px;
  display: flex;
  justify-content: space-around;
  transform: translateX(0px);
  transition: 1s all; }

img,
.slider {
  width: 960px;
  height: 640px;
  display: block; }

.radio-buttons {
  width: 170px;
  height: 30px;
  margin: 30px auto;
  display: flex;
  justify-content: space-around; }

.radio {
  width: 20px;
  height: 20px;
  border: 3px solid #e5e5e5;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; }

.active.radio::before {
  content: '';
  width: 10px;
  height: 10px;
  background-color: #e5e5e5;
  border-radius: 50%; }

.slider__left {
  position: absolute;
  top: calc(50% - 25px);
  left: 2%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid rgba(158, 158, 158, 0.801);
  background: rgba(255, 255, 255, 0.377);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s all; }

.slider__right {
  position: absolute;
  top: calc(50% - 25px);
  right: 2%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 3px solid rgba(158, 158, 158, 0.801);
  background: rgba(255, 255, 255, 0.377);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s all; }

.arrow__left {
  width: 15px;
  height: 15px;
  border-right: 3px solid rgba(66, 65, 65, 0.801);
  border-top: 3px solid rgba(66, 65, 65, 0.801);
  transform: rotate(-135deg);
  border-radius: 1px;
  position: absolute;
  left: 16px; }

.arrow__right {
  width: 15px;
  height: 15px;
  border-right: 3px solid rgba(66, 65, 65, 0.801);
  border-top: 3px solid rgba(66, 65, 65, 0.801);
  transform: rotate(45deg);
  border-radius: 1px;
  position: absolute;
  left: 13px; }

.slider__right:hover,
.slider__left:hover {
  transform: scale(1.4); }
